
import _ from 'lodash';

export default {
    props: {
        root: null,
        element: null,
    },
    methods: {
        getArray(name) {
            if (name === 'Property') {
                const definition = this.root.palete.items.find(item =>
                    item.content.component === _.upperFirst(_.camelCase(this.element.$options._componentTag))
                );
                const properties = [];
                if (definition && definition.inspector) {
                    Object.keys(definition.inspector).forEach(key => {
                        properties.push({
                            label: key,
                            key: key,
                            owner: this.value,
                            component: definition.inspector[key],
                        });
                    });
                }
                return properties;
            }
        },
        getItem(name) {
            return this.value[name];
        },
    },
};
