<template>
    <editor :value="innerValue" @input="change" :api-key="apiKey"></editor>
</template>
<script>
    import Editor from "@tinymce/tinymce-vue";
    export default {
        mixins: [],
        components: {
            Editor
        },
        props: {
            value: null,
            apiKey: null,
        },
        data() {
            return {
            };
        },
        computed: {
            innerValue() {
                if (this.value) {
                    const parsed = JSON.parse(this.value);
                    return parsed ? parsed.substr(5, parsed.length - 11) : "";
                }
                return '';
            },
        },
        methods: {
            change(value) {
                this.$emit("input", JSON.stringify("<div>" + value + "</div>"));
            }
        }
    };
</script>
