<template>
    <input :value="innerValue" @input="change">
</template>
<script>
    export default {
        mixins: [],
        props: {
            value: null,
            apiKey: null,
        },
        data() {
            return {
            };
        },
        computed: {
            innerValue() {
                if (this.value) {
                    const parsed = JSON.parse(this.value);
                    return parsed ? parsed : '';
                }
                return '';
            },
        },
        methods: {
            change(event) {
                this.$emit('input', JSON.stringify(event.target.value));
            }
        }
    };
</script>
